.searchContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
    background: #f6f6f6;
    margin: 0;
    width: 100%;
    height: 100%;
    overflow-y: auto;
    
}

.title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
}

.searchBar {
    display: flex;
    align-items: center;
    gap: 10px;
    margin: 60px auto;
}

.searchBar input {
    width: 300px;
    padding: 10px;
    border: 2px solid #ccc;
    border-radius: 4px;
}

.searchBar button {
    background: none;
    border: none;
    cursor: pointer;
}

.profileCard {
    background: #fff;
    border: 1px solid #ccc;
    border-radius: 20px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    margin:30px;
    width: 250px;
    height: 270px;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}

.profileCard img {
    border-radius: 50%;
    height: 110px;
    width: 110px;
    margin-bottom: 10px;
    margin-top: 20px;
}

.profileCard .name {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 3px;
}

.profileCard .username {
    font-size: 14px;
    color: #666;
    margin-bottom: 13px;
}

.profileCard .viewProfileButton {
    background: #ffcb00;
    border: none;
    border-radius: 4px;
    color: #FFF;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    padding: 10px 15px;
    cursor: pointer;
    text-decoration: none;
}
.profileCard .viewProfileButton:hover{
    background: #e5b800;
}

.notFoundMessage {
    text-align: center;
    
    margin-top: 20px;
}


.notFoundMessage h3{
    color: #000;
    font-family: Inter;
    font-size: 28px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

.notFoundMessage p {
  color: rgba(0, 0, 0, 0.42);
    text-align: center;
    font-family: Inter;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}